// import * as tome from 'chromotome';
import Engine from './utils/engine';


const app = new Engine(document.getElementById('canvas'), {
  debug: false,
  width: 480,
  height: 480,
  pixelRatio: 1
});

function arrToRgb(color) {
  return `rgb(${color[0]}, ${color[1]}, ${color[2]})`;
}

function loadImage(src) {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      resolve(img);
    }
    img.src = src;
  });
}

// const preColours = tome.getRandom().colors;

function drawPixel(x, y, size, color = [255, 100, 50]) {
  ctx.save();
  ctx.translate(x, y);
  let wid = size / 3;
  for (let i = 0; i < 3; i++) {
    let col = [0,0,0];
    col[i] = 255;
    ctx.fillStyle = arrToRgb(col);
    // ctx.fillStyle = preColours[i];
    ctx.fillRect(i * wid, 0, wid, size * (color[i] / 255));
  }
  ctx.restore();
}

function intrinsicHeight(destWid, destHei, wid, hei) {
  return (destWid / wid) * hei;
}

function intrinsicWidth(destWid, destHei, wid, hei) {
  return (destHei / hei) * wid;
}

let scale = 12;
let renderScale = 0.5;

app.onRender(({ frame, width, height, debug }) => {
  let scaledWid = width / scale;
  let scaledHei = height / scale;

  if (media.width >= media.height) {
    ctx.drawImage(media.el, 0, 0, intrinsicWidth(scaledWid, scaledHei, media.width, media.height), scaledHei);
  } else {

    ctx.drawImage(media.el, 0, 0, scaledWid, intrinsicHeight(scaledWid, scaledHei, media.width, media.height));
  }

  let imageData = ctx.getImageData(0, 0, scaledWid, scaledHei);
  ctx.fillStyle = 'black';
  ctx.fillRect(0, 0, width, height);

  ctx.save();
  ctx.translate(width * ((1 - renderScale) / 2), height * ((1 - renderScale) / 2));
  ctx.scale(1 * renderScale, 1 * renderScale);
  for (let i = 0; i < imageData.data.length; i += 4) {
    let x = (i / 4) % (scaledWid);
    let y = Math.floor((i / 4) / scaledHei);
    drawPixel(x * scale, y * scale, scale, [imageData.data[i], imageData.data[i + 1], imageData.data[i + 2]]);
  }
  ctx.restore();
});

const media = {
  el: false,
  width: 0,
  height: 0
};

function getWebcam() {
  return navigator.mediaDevices.getUserMedia({
    audio: false,
    video: true
  });
}

function createVideo(stream) {
  return new Promise((resolve, reject) => {
    const videoEl = document.createElement('video');
    videoEl.srcObject = stream;
    videoEl.style.position = 'fixed';
    videoEl.style.opacity = '0';
    videoEl.style.pointerEvents = 'none';
    videoEl.style.transform = 'scale(0)';
    document.body.appendChild(videoEl);

    const onCanPlay = () => videoEl.play();
    videoEl.addEventListener('canplay', e => {
      media.width = e.target.videoWidth;
      media.height = e.target.videoHeight;
      onCanPlay();
      videoEl.removeEventListener('canplay', onCanPlay);

      resolve(videoEl);
    });
  });
}

// loadImage(testImage)
app.preload((done) => {
  getWebcam()
    .then(createVideo)
    .then(done);
})
  .then(el => {
    media.el = el
    app.start();
  });
